<template>
  <div>
    <div class="container">
      <h1>Componentes</h1>
    </div>
    <ComponentAirplaneTable
        :data="componentAirplaneListState"
               @DeleteComponentAirplane="DeleteComponentAirplane"
                @addComponentAirplane="addComponentAirplane"
                @updateComponentAirplane="updateComponentAirplane"
/>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import ComponentAirplaneTable from "@/components/inventaryManagement/componentAirplane/ComponentAirplaneTable";
export default {
  name: "componentAirplane",
  components: {
    ComponentAirplaneTable
  },
  data () {
    return {
      jsonDataUpdate: {
        airPlaneTypeId: 0,
        createdAt: '',
        createdBy: '',
        cycle_ext: 0,
        cycle_prox_insp: 0,
        cycle_ult_insp: 0,
        description: '',
        fieldType: 0,
        hor_ext: 0,
        hor_prox_insp: 0,
        hor_ult_insp: 0,
        id: 0,
        location: 0,
        makerId: 0,
        planeQuantity: 0,
        prox_insp_date: '',
        reportNumber: '',
        status: '',
        subComponentList: [],
        timesTamp: '',
        totalCycles: 0,
        totalHours: 0,
        ult_insp_date: '',
        updatedAt: '',
        updatedBy: ''
      },
    }
  },
  created() {
    this.getComponentAirplaneState()
  },
  computed: {
    ...mapGetters(['componentAirplaneListState'])
  },
  methods: {
    ...mapActions(['getComponentAirplaneState', 'setComponentAirplanenew', 'apiUpdateComponentAirplane', 'apiDeleteComponentAirplane']),

    addComponentAirplane( item) {
      this.setComponentAirplanenew(item)
    },
    updateComponentAirplane( item) {
      this.jsonDataUpdate.reportNumber = item.reportNumber;
      this.jsonDataUpdate.description = item.description;
      this.jsonDataUpdate.status = item.status;
      this.jsonDataUpdate.planeQuantity = item.planeQuantity;
      this.jsonDataUpdate.id= item.id;
      this.jsonDataUpdate.totalHours = item.totalHours;
      this.jsonDataUpdate.totalCycles = item.totalCycles;
      this.jsonDataUpdate.planeQuantity= item.planeQuantity;
      this.apiUpdateComponentAirplane(this.jsonDataUpdate)
    },
    DeleteComponentAirplane(id) {
      this.apiDeleteComponentAirplane(id)
    }
  }
}
</script>

